
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import localStore from 'util/localStore'

export default {
data () {
return {
list: [],
extra: {},
baseUrl: process.env.DOMAIN_IP,
ifShare: false,
status: 1, // 0 未开始 1 进行中 2 已结束
tupGroup: localStore.get('TUP_group') 
}
},
methods: {
share () {
this.ifShare = true
},
wechatShare (type) {
this.$util.shareWebToWeChat(this.baseUrl + '/mobile-hsy/index.html#/wechatHccList', '圈粉达人挑战赛，理家顾问秀风采', '七大区域榜首已经诞生，谁是全国前三，5月25日由你揭晓', 'http://oss.tupperware.com.cn/static/images/2020-05-13/hcc_share.png', type)
this.ifShare = false
},
toCharts () {
this.$router.push({
path: '/hccCharts'
})
},
zan (index) {
let item = this.list[index]
if (this.status === 2) {
this.$vux.toast.text('哎呀，来晚了~当前时间已不支持点赞啦.')
return
}
if (item.isLike) {
this.$vux.toast.text('你已给TA点赞成功啦~热情已收到不需要重复点赞哦')
return
}
this.$tupHttp(this, `manage-app/hccActivity/like?cortId=${item.cort}`).then(res => {
this.list[index].isLike = 1
this.list[index].likeCount = this.list[index].likeCount + 1
this.$vux.toast.text('点赞成功！你已成功为达人献上点赞小心心~')
})
},
vote (index) {
let item = this.list[index]
if (this.status === 0 || this.status === 2) {
return
}
if (this.extra.voteCount <= 0) {
this.$vux.toast.text('你当前的剩余票数为0，不可以投票了哦')
return
}
if (item.isVote) {
this.$vux.toast.text('你已经成功投票啦~每个视频只可以投一次哦')
return
}
this.$tupHttp(this, `manage-app/hccActivity/vote?cortId=${item.cort}`).then(res => {
this.extra.voteCount = this.extra.voteCount - 1
this.list[index].isVote = 1
this.list[index].voteCount = this.list[index].voteCount + 1
this.$vux.confirm.show({
title: '投票成功',
content: '达人已收到你宝贵的投票，感谢你的支持和认可~',
showCancelButton: false,
onConfirm () {
}
})
})
},
_hccActivityFansInfo () {
this.$tupHttp(this, 'manage-app/hccActivity/hccActivityFansInfo', 'POST').then(res => {
this.list = res.data.models
this.extra = res.data.extra
if (res.data.extra.startTime) {
this.status = res.data.extra.startTime > new Date().valueOf() ? 0 : res.data.extra.endTime < new Date().valueOf() ? 2 : 1
}
this.$nextTick(() => {
for (let i = 0; i < this.list.length; i++) {
new Aliplayer({
id: 'player' + i,
source: this.list[i].videoUrl,
cover: this.list[i].coverUrl,
width: '100%',
height: '150px',
autoplay: false,
isLive: false,
rePlay: false,
playsinline: true,
preload: true,
controlBarVisibility: 'hover',
useH5Prism: true
}, function (player) {

})
}
})
})
}
},
created () {
this._hccActivityFansInfo()
},
components: {
XHeader,
Popup
}
}



